<template>
    <div class="an-user">
        <Row :space="9">
            <Cell :width="23">
                <div class="h-panel">
                    <div class="h-panel-bar">
                        <span class="h-panel-title primary-color">用户管理</span>
                    </div>
                    <div class="h-panel-body">
                        <Row :space="9">
                            <Cell :width="4">
                                <Select :disabled="isloading" v-model="data.kind" :datas="kinds" placeholder="选择日志类型"></Select>
                            </Cell>
                            <Cell :width="4">
                                <Search v-model="data.keyword" style="width: 100%"></Search>
                            </Cell>
                        </Row>
                        <br>
                        <Table :datas="datas" :loading="isloading">
                            <TableItem title="ID" prop="id"></TableItem>
                            <TableItem title="用户ID" prop="userId"></TableItem>
                            <TableItem title="目标ID" prop="businessId"></TableItem>
                            <TableItem title="日志类型" prop="kind"></TableItem>
                            <TableItem title="IP" prop="ip"></TableItem>
                            <TableItem title="创建时间" prop="createdAt"></TableItem>
                            <template slot="expand" slot-scope="{index, data}">
                                <Form readonly mode="twocolumn">
                                    <FormItem label="序号">{{index}}</FormItem>
                                    <FormItem label="姓名">{{data.name}}</FormItem>
                                    <FormItem label="年龄">{{data.age}}</FormItem>
                                    <FormItem label="地址">{{data.address}}</FormItem>
                                </Form>
                                <Loading :loading="data.loading"></Loading>
                            </template>
                            <div slot="empty">暂无数据</div>
                        </Table>
                        <br>
                        <Pagination v-model="pagination" @change="currentChange" :small="true" layout="total,sizes,pager,jumper"></Pagination>
                    </div>
                </div>
            </Cell>
        </Row>
    </div>
</template>

<script>
    export default {
        name: "user",
        data(){
            return {
                isloading: false,
                datas: []
            }
        }
    }
</script>

<style scoped lang="scss">

</style>
